import {FETCHED_SEARCH} from './actionType';

const initialState = {
	q: []
};


export default function search (state=initialState, action={}) {
	switch(action.thpe) {
		case FETCHED_SEARCH:
			return {
				...state,
				q: [action.search]
			};
		default:
			return state;
	}
}

export const getSearch = (state) => {
	return state.search.q;
};